import axios from 'axios'

export const API_URL = 'http://localhost:3000/api'

export default {
    namespaced: true,

    state: {
        products: [],
        categories: ['Smartphones', 'Laptops', 'Tablets', 'Accessories'],
        loading: false,
        error: null
    },

    getters: {
        getAllProducts: state => state.products,
        getAllCategories: state => state.categories,
        getProductById: state => id => state.products.find(p => Number(p.id) === Number(id)),
        getProductsByCategory: state => category => state.products.filter(p => p.category === category),
        getLoading: state => state.loading,
        getError: state => state.error
    },

    mutations: {
        SET_PRODUCTS(state, products) {
            state.products = products
        },
        SET_LOADING(state, status) {
            state.loading = status
        },
        SET_ERROR(state, error) {
            state.error = error
        }
    },

    actions: {
        async fetchProducts({ commit }) {
            commit('SET_LOADING', true)
            commit('SET_ERROR', null)

            try {
                // Log request
                console.log('Requesting products from:', `${API_URL}/products`)

                const response = await axios.get(`${API_URL}/products`)

                // Log successful response
                console.log('Raw server response:', response.data)

                // Handle different response structures
                let products = []
                if (response.data && (Array.isArray(response.data) || response.data.products)) {
                    products = Array.isArray(response.data) ? response.data : response.data.products
                    console.log('Processed products:', products)
                    commit('SET_PRODUCTS', products)
                    return products
                }

                throw new Error('Invalid data format received from server')

            } catch (error) {
                console.log('Full error details:', {
                    message: error.message,
                    response: error.response?.data,
                    status: error.response?.status
                })

                const errorMessage = 'Loading products failed. Checking server connection...'
                commit('SET_ERROR', errorMessage)
                throw new Error(errorMessage)
            } finally {
                commit('SET_LOADING', false)
            }
        },

        async addProduct({ commit, dispatch }, product) {
            commit('SET_LOADING', true)
            try {
                const formattedProduct = {
                    ...product,
                    price: Number(product.price),
                    stock: Number(product.stock)
                }
                const response = await axios.post(`${API_URL}/products`, formattedProduct)
                commit('ADD_PRODUCT', response.data)
                await dispatch('fetchProducts')
                return response.data
            } catch (error) {
                const errorMessage = error.response?.data?.message || error.message
                commit('SET_ERROR', errorMessage)
                throw new Error(errorMessage)
            } finally {
                commit('SET_LOADING', false)
            }
        },

        async deleteProduct({ commit }, productId) {
            commit('SET_LOADING', true)
            try {
                const response = await axios.delete(`${API_URL}/products/${Number(productId)}`)
                if (response.data.success) {
                    commit('DELETE_PRODUCT', productId)
                    return { success: true, message: response.data.message }
                }
                throw new Error(response.data.message || 'Delete operation failed')
            } catch (error) {
                const errorMessage = error.response?.data?.message || error.message
                commit('SET_ERROR', errorMessage)
                throw new Error(errorMessage)
            } finally {
                commit('SET_LOADING', false)
            }
        },

        async updateProduct({ commit, dispatch }, product) {
            commit('SET_LOADING', true)
            try {
                const formattedProduct = {
                    ...product,
                    id: Number(product.id),
                    price: Number(product.price),
                    stock: Number(product.stock)
                }
                const response = await axios.put(`${API_URL}/products/${formattedProduct.id}`, formattedProduct)
                commit('UPDATE_PRODUCT', response.data.data)
                await dispatch('fetchProducts')
                return response.data
            } catch (error) {
                const errorMessage = error.response?.data?.message || error.message
                commit('SET_ERROR', errorMessage)
                throw new Error(errorMessage)
            } finally {
                commit('SET_LOADING', false)
            }
        }
    }
}