import users from '@/data/users.json'
import axios from 'axios'

const API_URL = 'http://localhost:3000/api'

export default {
    namespaced: true,

    state: {
        users: [],
        updateStatus: null
    },

    getters: {
        getAllUsers: (state) => state.users,
        getUserById: (state) => (id) => state.users.find(u => u.id === id),
        getUpdateStatus: (state) => state.updateStatus
    },

    mutations: {
        SET_USERS(state, users) {
            state.users = users
        },
        UPDATE_USER(state, updatedUser) {
            const index = state.users.findIndex(u => u.id === updatedUser.id)
            if (index !== -1) {
                state.users[index] = updatedUser
            }
        },
        DELETE_USER(state, userId) {
            state.users = state.users.filter(u => u.id !== userId)
        },
        SET_UPDATE_STATUS(state, status) {
            state.updateStatus = status
        }
    },

    actions: {
        async fetchUsers({ commit }) {
            try {
                const response = await axios.get(`${API_URL}/users`)
                commit('SET_USERS', response.data)
            } catch (error) {
                commit('SET_USERS', users.users)
            }
        },

        async updateUser({ commit, rootState }, userData) {
            try {
                const currentUser = rootState.auth.currentUser
                const response = await axios.put(`${API_URL}/users/${currentUser.id}`, userData)
                commit('UPDATE_USER', response.data)
                commit('SET_UPDATE_STATUS', 'Profile updated successfully!')
                commit('auth/SET_USER', response.data, { root: true })
                localStorage.setItem('user', JSON.stringify(response.data))
                return response.data
            } catch (error) {
                commit('SET_UPDATE_STATUS', 'Update failed: ' + error.message)
                throw error
            }
        },

        // Update the deleteUser action
        async deleteUser({ commit }, userId) {
            try {
                const response = await axios.delete(`http://localhost:3000/api/users/${userId}`)
                if (response.status === 200) {
                    commit('DELETE_USER', userId)
                    return response.data
                }
            } catch (error) {
                console.log('Delete error:', error)
                throw error
            }
        }

    }
}