import orders from '@/data/orders.json'

export default {
    namespaced: true,

    state: {
        orders: [],
        loading: false,
        error: null
    },

    getters: {
        getAllOrders: (state) => state.orders,
        getUserOrders: (state) => (userId) => state.orders.filter(o => o.userId === userId),
        getOrderById: (state) => (id) => state.orders.find(o => o.id === id),
        getOrdersByStatus: (state) => (status) => state.orders.filter(o => o.status === status),
        getTotalOrders: (state) => state.orders.length,
        getTotalRevenue: (state) => state.orders.reduce((sum, order) => sum + (order.total || 0), 0)
    },

    mutations: {
        SET_LOADING(state, status) {
            state.loading = status
        },
        SET_ERROR(state, error) {
            state.error = error
        },
        SET_ORDERS(state, orders) {
            console.log('Setting orders in mutation:', orders)
            state.orders = orders || []
        },
        ADD_ORDER(state, order) {
            state.orders.push(order)
        },
        UPDATE_ORDER_STATUS(state, { orderId, status }) {
            const order = state.orders.find(o => o.id === orderId)
            if (order) {
                order.status = status
                order.updatedAt = new Date().toISOString()
            }
        }
    },

    actions: {
        fetchOrders({ commit }) {
            commit('SET_LOADING', true)
            try {
                console.log('Fetching orders:', orders)
                commit('SET_ORDERS', orders.orders)
                commit('SET_ERROR', null)
            } catch (error) {
                console.error('Error fetching orders:', error)
                commit('SET_ERROR', error.message)
            } finally {
                commit('SET_LOADING', false)
            }
        },

        createOrder({ commit, state }, orderData) {
            try {
                const newOrder = {
                    ...orderData,
                    id: state.orders.length ? Math.max(...state.orders.map(o => o.id)) + 1 : 1,
                    date: new Date().toISOString(),
                    createdAt: new Date().toISOString(),
                    updatedAt: new Date().toISOString()
                }
                commit('ADD_ORDER', newOrder)
                return newOrder
            } catch (error) {
                console.error('Error creating order:', error)
                throw error
            }
        },

        updateOrderStatus({ commit }, payload) {
            try {
                commit('UPDATE_ORDER_STATUS', payload)
            } catch (error) {
                console.error('Error updating order status:', error)
                throw error
            }
        }
    }
}
