export default {
    namespaced: true,

    state: {
        dashboardStats: {
            totalOrders: 0,
            totalUsers: 0,
            totalProducts: 0,
            totalRevenue: 0
        }
    },

    getters: {
        getDashboardStats: state => state.dashboardStats
    },

    mutations: {
        SET_DASHBOARD_STATS(state, stats) {
            state.dashboardStats = stats
        }
    },

    actions: {
        async getDashboardStats({ commit, rootState }) {
            // Calculate stats from existing store data
            const stats = {
                totalOrders: rootState.order.orders.length,
                totalUsers: rootState.user.users.length,
                totalProducts: rootState.product.products.length,
                totalRevenue: rootState.order.orders.reduce((sum, order) => sum + order.total, 0)
            }

            commit('SET_DASHBOARD_STATS', stats)
            return stats
        }
    }
}
